<template>
    <div class="articalDetails_03">
        <div class="art-base-info">
            <!-- <a href="#" style="font-size: 12px;">Online First, doi:10.32604/cmes.2020.011067 </a> -->
            <h3>
                Memristor-Based in-Situ Convolution Circuit with Kernel-Priority Convolution Algorithm
            </h3>
            <span style="display: block;color: #4f5671;line-height: 25px;margin-bottom: 10px;font-size: 12px;">
               Yingcheng Wu
            </span>
            <!-- <ul style="padding: 0;font-size: 12px;color: #4f5671;">
                <li>1 School of Computer Science and Engineering, University of Electronic Science and Technology of China, Chengdu, 611731, China</li>
                <li>2 Faculty of Engineering and Information Technology, University of Technology Sydney, NSW, 2007, Australia</li>
            </ul> -->
            <!-- <span style="font-size: 12px;color: #4f5671;">âˆ— Corresponding Author: Shiping Wen. </span> -->
        </div>

        <div class="download">
            <span><a href="https://stduestceducn-my.sharepoint.com/:b:/g/personal/201921080433_std_uestc_edu_cn/Ee3svSxocAdPh68yUPqdgpIB0K_PeUbIEQzc8C4xwv9MFQ?e=hcAOrE" rel="nofollow" class="pdf">Download PDF</a>  </span>
        </div>

        <div class="abstract">
            <h3>Abstract</h3>
            <p>
                Convolutional neural network performs high accuracy recognition and classification tasks. In the network,
a series of convolution operations are used to extract features
that can be further processed by a fully-connected layer. Despite
the great advantages of the convolution process, its operation is
not user-friendly enough to be directly implemented by using
memristors. In common practice, a memristor array is utilized
as the accelerator, which enhances the parallelism of the convolution computation but meanwhile increases the computational
complexity. In this paper, a memristor-based in-situ convolution
circuit is proposed with a kernel-priority convolution algorithm.
The convolution results are as good as the results generated
by professional software. The proposed circuit achieves a great
parallelism in performing convolution operations. The total computing cycles are reduced by more than 80% compared with the
state-of-the-art in-situ convolution accelerators. Moreover, fewer
memristors are needed and lower computational complexity are
achieved. The new design has a great potential in implementing
more efficient in-situ convolution neural networks.
            </p>
        </div>

        <div class="keywords">
            <h3>Keywords</h3>
            <p>Memristor, convolution operation, neural network, parallel algorithm</p>
        </div>


    </div>
</template>

